// Mix-ins:

@mixin center {
  display: flex;
  align-items: center;
  justify-content: center;
}

// Font-faces:

@font-face {
  font-family: Inter;
  src: url(../fonts/Inter-ExtraBold.ttf);
}
@font-face {
  font-family: SourceSans;
  src: url(../fonts/SourceSansPro-Light.ttf);
}

// General settings:

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-decoration: none;
  &::selection {
    background-color: rgba(112, 112, 112, 0.658);
    color: rgb(255, 255, 255);
  }
}

body {
  background: url(../img/bg.jpg) center/cover fixed;
  &::before {
    content: "";
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background: black;
    animation: blackBefore 1.8s ease forwards;
    animation-delay: 1s;
    z-index: 100;

    @keyframes blackBefore {
      to {
        opacity: 0;
        z-index: -1;
      }
    }
  }
}

html {
  scroll-behavior: smooth;
}

.img-container {
  position: relative;
  &:hover {
    .img-description {
      animation: imgDescriptionAnim 0.6s ease forwards;
      @keyframes imgDescriptionAnim {
        to {
          opacity: 1;
        }
      }
    }
  }
}

.img-description {
  position: absolute;
  bottom: 0;
  font-family: SourceSans;
  font-weight: bold;
  background: rgb(255, 255, 255);
  opacity: 0;
}

// Animations:

@keyframes wobble {
  0% {
    transform: translateY(2px);
  }
  25% {
    transform: translateY(-2px);
  }
  50% {
    transform: translateY(2px);
  }
  75% {
    transform: translateY(-2px);
  }
  100% {
    transform: translateY(0);
  }
}

