header {
  position: relative;
  color: white;
  padding: 50px 0 0 0;
  height: 100vh;
  .header-container {
    transform: translateY(60px);
    opacity: 0;
    animation: headerAnim 1.8s forwards;
    @keyframes headerAnim {
      to {
        transform: translateY(0);
        opacity: 1;
      }
    }
  }
  .title-container {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  h1 {
    height: 5.6rem;
    font-size: 2.8rem;
    text-align: center;
    font-family: Inter;
    text-transform: uppercase;
    max-width: 550px;
  }
  #title {
  }
  p {
    font-style: italic;
    font-family: SourceSans;
    text-align: center;
    font-size: 1.1rem;
    line-height: 28px;
    max-width: 400px;
    display: block;
    margin: 0 auto;
    padding: 30px 0 60px 0;
  }
  .arrow {
    @include center;
    padding-bottom: 80px;
    a {
      color: white;
      i {
        font-size: 2rem;
        border: 1px solid white;
        border-radius: 100px;
        display: inline-block;
        width: 48px;
        height: 48px;
        @include center;
        &:hover {
          animation: wobble 0.5s linear;
        }
      }
    }
  }
}
