main {
  min-height: 1000px;
  margin: 0 10vh;
  background: white;
  h2 {
    text-transform: uppercase;
    font-family: Inter;
    font-size: 2.2rem;
    text-align: center;
    padding: 20px 0;
    letter-spacing: 3px;
  }
  .intro .img-description {
    right: 15%;
    padding: 5px;
    font-size: 0.9rem;
  }
  img {
    display: block;
    margin: 0 auto;
    width: 70%;
  }
  p {
    display: block;
    margin: 0 auto;
    font-style: italic;
    padding: 20px 0 10px 0;
    max-width: 500px;
    text-align: center;
    font-family: SourceSans;
    line-height: 25px;
  }
  .container {
    padding: 15px;
  }
  .section-1,
  .section-2 {
    box-shadow: inset 0 1px 0 rgb(216, 216, 216);
    p {
      text-align: left;
      max-width: 380px;
    }
    img {
      width: 100%;
    }
    .section-box {
      @include center;
      &:nth-child(1) {
        padding-left: 10%;
      }
      &:nth-child(2) {
        padding-right: 10%;
      }
    }
    .img-description {
      right: 0;
      font-size: 0.7rem;
      padding: 3px;
    }
  }
  .section-1 {
    padding-left: 15px;
    margin-top: 20px;
    display: grid;
    align-items: center;
    grid-template-columns: 35% 65%;
    min-height: 400px;
  }
  .section-2 {
    padding-right: 15px;
    display: grid;
    align-items: center;
    grid-template-columns: 65% 35%;
    min-height: 400px;
  }
}
