.input-container {
  padding: 10px;
  text-align: center;
  input {
    padding: 5px;
    font-size: 1rem;
  }
}

.ask-container {
  border: 1px solid rgb(157, 157, 157);
  margin: 0 10%;
  min-height: 50vh;
  overflow: hidden;
  #answersBox,
  #questionsBox {
    display: block;
    width: 100%;
    height: 60px;
    position: relative;
  }
  #answer,
  #question {
    position: absolute;
    font-family: SourceSans;
    font-weight: bold;
    display: inline-block;
    margin: 10px;
    padding: 10px;
  }
  #answer {
    background: linear-gradient(90deg, rgb(254, 143, 254), rgb(255, 189, 255));
    border-radius: 20px 20px 20px 0;
  }
  #question {
    background: linear-gradient(90deg, rgb(136, 170, 255), rgb(83, 149, 255));
    border-radius: 20px 20px 0 20px;
    right: 0;
  }
}
