nav {
  position: absolute;
  bottom: 0;
  left: 10vh;
  right: 10vh;
  z-index: 2;
  background: rgba(255, 255, 255, 0.234);
  // animation: navbarAnim 1.8s ease forwards;
  // @keyframes navbarAnim {
  //   to {
  //     bottom: 0;
  //   }
  // }
  .nav-items {
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .nav-pages {
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:hover a:not(:hover) {
      li {
        color: whitesmoke;
      }
      background: none;
    }
    a {
      font-size: 0.9rem;
      letter-spacing: 1px;
      padding: 0 18px;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 50px;
      text-transform: uppercase;
      font-family: Inter;
      color: whitesmoke;
      &:hover {
        background-color: whitesmoke;
        li {
          color: black;
        }
      }
    }
    .active-page {
      background-color: whitesmoke;
      li {
        color: black;
      }
    }
  }
  .nav-social {
    height: 50px;
    display: flex;
    min-width: 200px;
    align-items: center;
    justify-content: end;
    li {
      padding: 0 20px;
      height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      a {
        color: whitesmoke;
        i {
          font-size: 1.4rem;
        }
      }
      &:hover {
        background-color: whitesmoke;
        a {
          color: black;
        }
      }
    }
  }
}
