footer {
    background: white;
    margin: 30px 10vh 0 10vh;
    min-height: 60px;
    font-family: SourceSans;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .bg-credits {
        padding-left: 10px;
        a {
            color: black;
            text-decoration: underline;
            &:hover {
                color: rgb(60, 60, 60);
            }
        }
    }
    .email {
        padding-right: 20px;
        address {
            display: inline;
        }
    }
}