.knowledge-box {
  background: rgb(240, 240, 240);
  min-height: 400px;
  color: rgb(40, 40, 40);
  display: grid;
  transition: 1s ease;
  &:hover {
    background: rgb(230, 230, 230);
  }
  @media screen and (max-width: 890px) {
    display: block;
    p {
      margin: 0 2%;
    }
  }
  .text-container {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  h3 {
    font-size: 1.5rem;
    font-family: Inter;
    text-align: center;
    @media screen and (max-width: 890px) {
      padding-top: 20px;
    }
  }
  p {
    font-style: normal;
    font-size: 1.1rem;
  }
}

.python {
  grid-template-columns: 60% 40%;
  .text-container {
    flex-direction: column;
  }
  strong {
    font-family: SourceSans;
    font-size: 1.2rem;
  }
  .ability-box {
    padding-top: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    .ul-container {
      display: flex;
      justify-content: space-between;
      @media screen and (max-width: 890px) {
        flex-direction: column;
      }
      ul {
        li {
          padding: 10px;
          text-align: center;
          font-family: SourceSans;
        }
        width: 200px;
      }
    }
  }
  .img-container {
    background: url(../../img/python.webp) center/cover;
  }
}

.html-scss {
  @media screen and (max-width: 890px) {
    border-top: 1px dashed rgb(96, 96, 96);
  }
  min-height: 450px;
  grid-template-columns: 40% 60%;
  .img-container {
    background: url(../../img/html.webp) center/cover;
  }
}

.js-react {
  @media screen and (max-width: 890px) {
    border-top: 1px dashed rgb(96, 96, 96);
  }
  grid-template-columns: 60% 40%;
  .text-container {
    a {
      color: rgb(40, 40, 40);
      text-decoration: underline;
      &:hover {
        font-weight: bold;
      }
    }
  }
  .img-container {
    background: url(../../img/js-react.webp) center/cover;
  }
}
